body {
  background-color: #f9f9f9;
  margin: 20px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.loader {
  color: #ff4500;
  font-weight: bold;
}

.error {
  color: red;
  font-weight: bold;
}

.cat-info img {
  cursor: pointer;
  border: 5px solid #ff4500;
  border-radius: 20px;
  width: 100%;
  max-width: 350px;
  height: 100%;
  margin: 10px;
  padding: 5px;
  transition: transform .5s;
}

.cat-info img:hover {
  transform: scale(1.1);
}

.cat-info h2 {
  color: #ff4500;
  font-family: Georgia, Times New Roman, Times, serif;
}

.ss-main {
  color: #ff4500;
  background-color: #f9f9f9;
  border: 1px solid #ff4500;
  border-radius: 5px;
  width: 100px;
  height: 40px;
  margin: 50px 0;
}
